import React from "react"
import { StaticQuery } from "gatsby"
import Img from "gatsby-image"
import Layout from "../components/layout"
import { Helmet } from "react-helmet"

import "./../styles/about.scss"

export default ({ data }) => {
    return (
	    <StaticQuery
	    query={graphql`
		    query {
			    allFile(filter: {relativePath: {eq: "img/1646.jpeg"}}) {
				    edges {
					    node {
						    relativePath
						    childImageSharp {
							    fluid {
								    ...GatsbyImageSharpFluid
							    }
						    }
					    }
				    }
			    }
		    }
	    `}
	    render={data1 => {return(
        <div className="global about large-header">
            <Layout>
                <Helmet>
                    <title>About | Hana Renee</title>
                </Helmet>

                <div className="content">
                <p>Hana is a photographer from Baltimore, MD based in Brooklyn, NY.</p><p>Feel free to reach out with questions, print requests, or feedback.</p>
		    <div className="formPicture">
		    <Img fluid={data1.allFile.edges[0].node.childImageSharp.fluid} />
 <form action="https://formspree.io/hanaclemens@gmail.com" method="POST">
  <input type="text" name="name" placeholder="Name" />
  <input type="email" name="_replyto" placeholder="E-mail" />
  <textarea rows="30" name="message" placeholder="Message" id="message" />
  <input type="submit" value="Send" />
</form> 
		    </div>
                </div>


            </Layout>
        </div>
    )}}/>)
}
